import classes from './DialogsMessages.module.css'
import DialogsTextArea from "./DialogsTextArea/DialogsTextArea";
import {useMemo} from "react";
import Loading from "../../../common/Loading/Loading";


function DialogsMessages(props) {

    let Messages = ({messages}) => useMemo(
        () => messages.map(el => <div key={el.id}
                                           className={el.senderId === props.id
                                               ? classes.item
                                               : `${classes.item} ${classes.second_item}`}>
                <div className={el.senderId === props.id ?
                    classes.message : classes.second_message}
                     dangerouslySetInnerHTML={{__html: el.body}}></div>
            </div>
        )
    ,[props.dialogsPage.messages])

    if (props.loading) return <Loading/>
    else return (
        <div className={classes.messages}>
            <div className={classes.body}>
                <Messages messages={props.dialogsPage.messages}/>
            </div>
            <DialogsTextArea newMessageText={props.dialogsPage.newMessageText}
                             addNewMessageText={props.addNewMessageText}
                             userId={props.userId}
                             addNewMessage={props.addNewMessage}/>
        </div>

    )
}

export default DialogsMessages